import { Component, OnInit } from '@angular/core';
import { FormGroup, FormControl, Validators } from '@angular/forms';
import { ProfileServiceService } from '../service/profile-service.service';
import { Router } from '@angular/router';

@Component({
  selector: 'app-home-page',
  templateUrl: './home-page.component.html',
  styleUrls: ['./home-page.component.css']
})
export class HomePageComponent implements OnInit {

  registerForm: FormGroup;
  users: any[] = [];
  submitted: boolean = false;
  // submit_btn_text: string = "Submit";
  profileDP :any= '../../assets/default_img.png';
  emailPattern = "^[a-z0-9._%+-]+@[a-z0-9.-]+\.[a-z]{2,4}$";

  

  get photo() {
    return this.registerForm.get('photo');
  }
  get firstName() {
    return this.registerForm.get('firstName');
  }
  get lastName() {
    return this.registerForm.get('lastName');
  }
  get email() {
    return this.registerForm.get('email');
  }
  get mobile() {
    return this.registerForm.get('mobile');
  }
  get age() {
    return this.registerForm.get('age');
  }
  get state() {
    return this.registerForm.get('state');
  }
  get country() {
    return this.registerForm.get('country');
  }
  get address() {
    return this.registerForm.get('address');
  }
  

  constructor(private httpService: ProfileServiceService, private router: Router) { }

  ngOnInit() {
    this.httpService.getAllProfile();

    this.registerForm = new FormGroup( {
      photo: new FormControl('', Validators.compose([Validators.required]) ),
      firstName: new FormControl('', Validators.compose([Validators.required,Validators.minLength(2),Validators.maxLength(20), Validators.pattern("[a-zA-Z][a-zA-Z ]+") ]) ),
      lastName: new FormControl('', Validators.compose([Validators.required, Validators.minLength(2), Validators.pattern("[a-zA-Z][a-zA-Z ]+") ]) ),
      email: new FormControl('', Validators.compose([Validators.required, Validators.pattern(this.emailPattern) ]) ),
      mobile: new FormControl('', Validators.compose([Validators.required, Validators.minLength(10), Validators.maxLength(10) ]) ),
      age: new FormControl('', Validators.compose([Validators.required, Validators.min(20), Validators.max(60) ]) ),
      state: new FormControl(''),
      country: new FormControl(''),
      address: new FormControl(''),
      tags: new FormControl(''),
      subscribe: new FormControl('')
    } )

  }

  ImageUpload(event: any){
    this.profileDP = event.target.file;
  }


  formValue() {
    this.httpService.saveProfileData(this.registerForm.value).subscribe( (data)=>{
      // console.log(data);
    } )
    alert('register successfull');
    setInterval( ()=>{
      this.router.navigate(['/profile']);
    },1000 )
    
    console.log(this.registerForm.value);
  }

  //cancel btn
  formCancel(){
    this.registerForm.reset();
  }


}
